import React from "react";
import MultiCartItemAttributeComponent from "./MultiCartItemAttributeComponent";

export default class MultiCartItemDetailMobileComponent extends MultiCartItemAttributeComponent {
    constructor (props) {
        super(props);
    }

    render() {
        return (
            <div className={this.props.className}>
                { this.props.price ?
                    this.props.title.replace('%1', this.getPartsOfPrice(this.props.price.price).join(',') + ' ' + this.props.price.currency)
                    : this.props.title
                }
            </div>
        )
    }
}